import React from 'react';
import styled from 'styled-components'


const Contact = () => {
    return (
<div className="flex justify-center">
    <div className="flex flex-wrap max-w-screen-sm mb-6 shadow-md rounded-md">
        <form className="w-full py-6 px-4 ">
            <h2 className="font-medium text-gray-600 text-2xl my-4">Get in touch</h2>
            <div className="flex flex-wrap -mx-3 mb-6">
                <div className="w-full md:w-1/2 px-3 mb-6">
                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="full-name">
                        Full Name
                    </label>
                    <input
                        className="appearance-none block w-full bg-gray-100 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                        id="full-name" type="text" placeholder="Mark Florence" required />
                </div>
                <div className="w-full md:w-1/2 px-3 mb-6 ">
                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="email">
                        email
                    </label>
                    <input
                        className="appearance-none block w-full bg-gray-100 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                        id="email" type="email" placeholder="example@xzy.com" required />
                </div>
            </div>
            <div className="flex flex-wrap -mx-3 mb-6">
                <div className="w-full px-3">
                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="company">
                        Company
                    </label>
                    <input
                        className="appearance-none block w-full bg-gray-100 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                        id="company" type="text" />
                </div>
            </div>
            <div className="flex flex-wrap -mx-3 mb-6">
                <div className="w-full px-3">
                    <label className="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="message">
                        Message
                    </label>
                    <textarea
                        className="appearance-none block w-full bg-gray-100 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500"
                        id="message" placeholder="Your message here...">
                    </textarea>
                </div>
            </div>
            <button type="submit"
                className="float-right transition duration-500 ease-in-out bg-blue-400 hover:bg-blue-600 transform hover:-translate-y-1 hover:scale-110 text-white font-bold h-16 w-16 transform rounded-full">
                <i className="-ml-1 fa-2x far fa-paper-plane"></i>
            </button>
        </form>
    </div>
</div>
    );
};

export default Contact;